import { Index } from './index';

export class RSU{
	rsuTarget: string;
	rsuUsername: string;
	rsuPassword: string;
	rsuRetries: string;
	rsuTimeout: string;
	isSelected: boolean;
	index: number;
	indicies_set: Index[];
	indicies: number[];

	constructor() {
        this.rsuRetries = "1";
        this.rsuTimeout = "2000";
    }
}


